@import "init";

html, body, .layout-container, main, .pane, .scroll-wrapper {
  height:100%;
}

html {
  overflow: hidden;
}

.not-front .region {
  padding-top: 50px;
}
.path-frontpage {
  $total_width: ($pane_count * $pane_width) + (($pane_count - 1) * $pane_margin);

  .page-main {
    position: relative;
    width: $total_width;
  }
  .pane {
    float:left;
    width:percentage($pane_width / $total_width);
    margin-right:percentage($pane_margin / $total_width / 2);
    margin-left:percentage($pane_margin / $total_width / 2);
    z-index: 10;
    &.last {
      margin-right:0;
    }
    &.first {
      margin-left:0;
    }
    .region {
      background: rgba(255, 255, 255, .8);
      position: absolute;
      width: 100%;
      max-width: 1000px;
      margin-left: auto;
      margin-right: auto;
      left: 0;
      right: 0;
      display: block;
      top: 0;
      padding: 48px 0 350px;
      bottom: $parallax_bg_horizon;
      overflow-x: visible;
      overflow-y: auto;
      margin-top: 61px;
      &.region-content {
        padding-top: 37px;
        background: none;
        overflow-y: hidden;
        padding-bottom: 0;
        .slogan {
          margin-top: 60px;
        }
      }
      &.region-blog {
        overflow-y: scroll;
      }
      &.region-contact {
        background: none;
        overflow-y: hidden;
      }
      .block {
        margin-left:auto;
        margin-right:auto;
      }
    }
  }
}


.not-front {
  .page-main {
    padding-top:$nav_height_wide;
  }
}

.pane {
  position: relative;
  width:100%;
}

.bg-train {
  bottom:15px;
  width:1288px;
  height:67px;
  z-index: 49;
}

.iscroll-processed {
  .bg-container {
    .bg-background, .bg-foreground, .bg-tracks {
      position: absolute;
    }
  }
  .bg-train {
    position: absolute;
    right: 93% !important;
    &.train-dislodged-behind {
      animation: iscrollDislodgeTrainBehind 0.5s ease-out;
      right: 93.066% !important;
    }
    &.train-dislodged-forward {
      animation: iscrollDislodgeTrainForward 0.5s ease-out;
      right: 93% !important;
    }
  }
}

.no-iscroll {
  .bg-train {
    position: fixed;
    backface-visibility: hidden;
    right: 50% !important;
    &.train-dislodged-behind {
      animation: dislodgeTrainBehind 0.5s ease-out;
      right: 50.166% !important;
    }
    &.train-dislodged-forward {
      animation: dislodgeTrainForward 0.5s ease-out;
      right: 50% !important;
    }
  }
}

.bg-container {
  position: absolute;
  bottom:0;
  height:$parallax_bg_height_large;
  width:100%;
  overflow: visible;
  .bg-background, .bg-foreground, .bg-tracks, .bg-sign-container {
    position: absolute;
    height:100%;
    width:100%;
    background:transparent bottom left repeat-x;
  }
  .bg-background {
    height: $parallax_bg_height_large;
    width: 6400px;
  }
  .bg-sign-container {
    width: 6400px;
    bottom: 165px;
    z-index: 49;
    // 0 height is important. It prevents
    // the div from overlapping content.
    height: 0px;
    overflow: visible;
  }
  .bg-foreground {
    z-index: $header_parallax_bg_zindex;
    height: $parallax_bg_horizon;
    width: 9600px;
    bottom: 0;
  }
  .bg-tracks {
    bottom: -3px;
  }
}

.d8-sign {
  width: 205px;
  height: 109px;
  margin-left: 865px;
  a {
    width: 100%;
    height: 90px;
    display: block;
  }
}

.block {
  #pane-about & {
    margin: 0 40px;
    max-width: 970px;
  }
  #pane-blog & {
    max-width: 640px;
  }
  > h2 {
    #pane-blog &, #pane-about & {

    }
  }
}

#pane-blog {
  .region {
    padding-top: 32px;
  }
}

.mobile-menu,
#title,
.menu-title {
  display: none;
}

// About
.profile {
  &.compact {
    width: 221px;
    &:hover {
      margin-left: -10px;
      margin-right: -10px;
      padding: 10px 20px;
      .field--name-field-job-title {
        display: block;
        clear: both;
        position: relative;
        padding-top: 5px;
        margin-bottom:5px;
      }
      .field--name-field-full-name {
        position: relative;
        top: inherit;
        padding-top: 10px;
      }
    }

    .views-row:last-child & {
      &:hover {
        margin-right: 0;
        margin-left: 0;
        padding: 10px;
        .field--name-field-full-name {
          top: 59px;
        }
        .field--name-field-job-title {
          top: 130px;
        }
      }
    }
  }
}

#block-views-block-employees-staff-block {
  p {
    text-align: left;
    margin-bottom: 15px;
    margin-top:0;
    &.join-us-button {
      float: right;
      margin-top: 10px;
    }
  }

  .view-header {
    margin-bottom: 35px;
  }
  h1.title {
    text-align: left;
    margin-top: 0;
    margin-bottom: 0;
  }

  .views-row {
    float: left;
    width: 220px;
    height: 220px;
    overflow: visible;
    margin: 0px 3px 20px 3px;
  }

  .view-footer {
    clear: both;
    padding-top: 50px;
    padding-bottom: 100px;
  }
}

// Work.
#pane-work {
  .block {
    margin: 0 40px;
    display: block;
    float: none;
  }
}

#block-views-block-portfolio-block-1 {
  .view-header {
    margin-bottom: 5px;
  }
  .views-row {
    width: 48.65%;
    float: left;
    display: block;
    &:nth-child(odd) {
      margin-right: 1.35%;
      clear: both;
    }
    &:nth-child(even) {
      margin-left: 1.35%;
    }
  }
}

.node--type-portfolio {
  &.node--view-mode-teaser {
    width: 100%;
    height: auto;
    margin-bottom: 5px;
    img {
      width: 100%;
      height: auto;
    }
  }
  &.node--view-mode-full {
    width: 960px;
    margin: 0 auto;

    p {
      margin-bottom: 25px;
    }
    .portfolio-button {
      display: none;
    }
    .field.field--name-field-cover-image,
    .field.field--name-field-site-screenshots img {
      float: left;
      width: 100%;
      height: auto;
    }
    .field.field--name-field-site-screenshots {
      .cycle-slide {
        img {
          margin: 0;
          width: 100%;
          height: auto;
        }
      }
      .cycle-pager {
        float: left;
        margin-left: -1%;
        margin-right: -1%;
        img {
          width: 31.333%;
          margin: 10px 1% 0;
        }
      }
    }
    .field--name-field-site-screenshots {
      max-width: 62%;
      width: 585px;
      min-height: 420px;
      margin-right: 3%;
      float: left;
    }
    .field--name-body {
      p {
        margin-top: 0px;
      }
    }
    blockquote {
      p {
        margin-bottom: 0;
      }
    }
    .image-style-portfolio-feature-image {
      margin-bottom: -6px;
    }
  }
}

#block-views-block-blog-latest-posts-block-1 h2 {
  margin-bottom: 7px;
}

.portfolio-button {
  padding: 5px;
  display: inline-block;
}

.touch {
  .region {
    bottom: $parallax_bg_horizon;
    padding-top: 90px !important;
  }
  .pane {
    overflow: hidden;
    bottom: 60px;
  }
}

.node--type-portfolio.node--view-mode-full {
  .node--content {
    width: 35%;
    float: right;
  }
}

#block-portfoliologos {
  position: relative;
  z-index: 0;
  #container {
    display: table;
    width: 100%;
  }
  h2 {
    margin: 10px;
  }
  .portfolio-logos-slideshow {
    display: table-cell;
    width: 20%;
    height: 65px;
    float: left;
    img {
      width: auto;
      max-width: 110px;
      height: auto;
      max-height: 65px;
    }
  }
}

.exception-title {
  display: none;
}

.exception {
  .exception-title {
    display: block;
  }
  .page-main {
    padding-top: 0;
    text-align: center;
  }
  #pane-home {
    &:before {
      content: '';
      height: 65%;
      vertical-align: middle;
      display: inline-block;
    }
    .region-content {
      padding-top: 3px;
      p {
        margin: 6px;
        padding: 0;
        line-height: 1em;
      }
    }
    .layout-content {
      padding: 0 50px;
      display: inline-block;
      vertical-align: middle;
    }
    .exception-title {
      display: block;
      margin: 0 0 20px 0;
      line-height: .95em;
    }
  }
}
