.iscroll-processed .bg-container .bg-background, .iscroll-processed .bg-container .bg-foreground, .iscroll-processed .bg-container .bg-tracks {
  position: absolute; }

.iscroll-processed .bg-train {
  position: absolute;
  right: 93% !important; }
  .iscroll-processed .bg-train.train-dislodged-behind {
    animation: iscrollDislodgeTrainBehind 0.5s ease-out;
    right: 93.066% !important; }
  .iscroll-processed .bg-train.train-dislodged-forward {
    animation: iscrollDislodgeTrainForward 0.5s ease-out;
    right: 93% !important; }

.no-iscroll .bg-train {
  position: fixed;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  right: 50% !important; }
  .no-iscroll .bg-train.train-dislodged-behind {
    animation: dislodgeTrainBehind 0.5s ease-out;
    right: 50.166% !important; }
  .no-iscroll .bg-train.train-dislodged-forward {
    animation: dislodgeTrainForward 0.5s ease-out;
    right: 50% !important; }

.bg-container {
